@import url(
'https://fonts.googleapis.com/css2?family=Pacifico&display=swap');
@import url(
'https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,400;0,600;0,800;1,300&display=swap');

.App {
  height: 121vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #874242;
  background-image: linear-gradient(315deg, #dfcaca 0%, #cd6969 74%);
  font-family:'Poppins', sans-serif;
}

h1 {
  text-align: center;
  font-size: 3em;
  font-family:'fantasy';
  color: #5310c6;
  padding: 1.5em;
}

h2{
  font-size: 30px;
  text-decoration: underline;
  padding-bottom: 20px;
}

h4{
  color: #2e1489;
}

input{
  width: 500px;
  height: 55px;
  font-size: 20px;
  padding: 5px;

}

.searchBox > button{
  background-color: #4b7444;
  height: 70px;
  width: 80px;
  padding: 14px;

  color: white;
  cursor: pointer;
  
}

.showResults{
  width: 500px;
  padding: 20px;
}

.showResults > h2 > button{
  background: none;
  border: none;
  cursor: pointer;
}